/* eslint-disable */
const validate = (values) => {
  const errors = {};
  if (!values.name) {
    errors.name = "Username field shouldnt be empty";
  }
  if (!values.email) {
    errors.email = "Email field shouldnt be empty";
  } else if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,4}$/i.test(values.email)) {
    errors.email = "Invalid email address";
  }
  if (!values.url) {
    errors.url = "Url field shouldnt be empty";
  } else if (
    !/^(https?:\/\/)?((([a-z\d]([a-z\d-]*[a-z\d])*)\.)+[a-z]{2,}|((\d{1,3}\.){3}\d{1,3}))(\:\d+)?(\/[-a-z\d%_.~+]*)*(\?[;&a-z\d%_.~+=-]*)?(\#[-a-z\d_]*)?$/i.test(
      values.url
    )
  ) {
    errors.url = "Invalid url";
  }
  if (!values.password) {
    errors.password = "Password field shouldnt be empty";
  } else if (values.password.length < 3) {
    errors.password = "The password should be greater than 4";
  }
  if (!values.select) {
    errors.select = "Please select the option";
  }

  return errors;
};

export default validate;
